<template lang="pug">
    baidu-map(
        id="map" ak="1taMaGAUGBb2S4gQvHqDt7daPOCIjIkW" 
        :center="center"   
        :zoom="zoom"
        @touchstart="closeAll"
        )
        bm-control
            button(class="home-btn icon-charger-filter" @click="chargerSearchShow") 筛选
        bm-scale(anchor="BMAP_ANCHOR_BOTTOM_LEFT")
        bm-navigation(anchor="BMAP_ANCHOR_TOP_LEFT")
        bm-geolocation(anchor="BMAP_ANCHOR_BOTTOM_LEFT" :showAddress="true" :autoLocation="true")
        div(v-for="item in addressFilter")
            my-overlay(:position="{lng:item.mapPosition[0],lat:item.mapPosition[1]}"
                       :class="{'icon-gray':item.status==chargerStatus.OFFLINE,'icon-blue':item.status==chargerStatus.ONLINE,'icon-yellow': item.status==chargerStatus.TROUBLE,'icon-green':item.status==chargerStatus.CHARGING}"
                       @showChargerInfo="showChargerInfo"
                       :item="item"
                      )
        charger-info(
            :chargershow="chargerInfoShow"
            :address="this.addressOrder.address"
            :deviceId="this.addressOrder.deviceId"
            :status="this.addressOrder.status"
            :energyMoney="this.addressOrder.energyMoney"
            :serviceMoney="this.addressOrder.serviceMoney"
            @close="close"
        )
        charger-search(
            :searchShow="searchShow"
            @searchClose="searchClose"
            @confirm="confirmStatus"
        )
</template>

<script>
    import {mapState} from 'vuex'
    import {BaiduMap,BmNavigation,BmGeolocation,BmScale,BmOverlay,BmControl}  from 'vue-baidu-map'
    import MyOverlay from '../components/MyOverlay.vue'
    import ChargerInfo from '../components/ChargerInfo.vue'
    import ChargerSearch from '../components/ChargerSearch.vue'
    import { CHARGER_STATUS } from '../common/ChargerStatus.js'
    import {getAllChargerInfo} from '../api/home'
    import {getLocation} from '../utils/wxJSSDK'

    export default {
        name:'home',
        components:{
            BaiduMap,
            BmNavigation,
            BmGeolocation,
            BmScale,
            BmOverlay,
            MyOverlay,
            BmControl,
            ChargerInfo,
            ChargerSearch
        },
        data(){
            return {
                addressList:[],
                addressFilter:[],
                chargerInfoShow: false,
                searchShow: false,
                addressOrder: [],
                chargerStatus:CHARGER_STATUS,
                zoom:15
            }
        },
        computed:{
            addressListFilter () {
             return this.addressList
           },
           ...mapState('user', {
               userId: state=>state.userInfo.userId
           }),
           center:function(){
               let res = getLocation()
               alert(JSON.stringify(res)+"asdasdasda")
               return {lng:res.longitude||113.9519,lat:res.latitude||22.5903}
           }
        },
        mounted(){
            this.init()
        },
        methods:{
            init(){
                getAllChargerInfo({
                    userId:this.userId
                }).then((res)=>{
                    console.log(res)
                    this.addressList=res
                    this.addressFilter=this.addressList
                    console.log(this.addressFilter);
                }).catch(error=>{
                    this.$store.commit('stateBox/popUpToast',{
                        text: '服务器开小差,稍后再试',
                        display: true
                    });
                })
            },
            showChargerInfo (item) {
                console.log(item)
                this.addressOrder = item
                this.chargerInfoShow = true
                this.searchShow = false
            },
            //点击X关闭详情框
            close () {
                this.chargerInfoShow = false
            },
            //外部关闭详情框
            closeAll () {
               this.chargerInfoShow = false
               this.searchShow = false
            },
            searchClose () {
                this.searchShow = false
            },
            chargerSearchShow () {
                this.searchShow = !this.searchShow
                this.chargerInfoShow = false
            },
            // 筛选触发后执行
            confirmStatus(data){
                this.addressFilter = []
                if (data !== 100) {
                    this.addressList.forEach((item, index) => {
                        if (item.status === data) {
                        this.addressFilter.push(this.addressList[index])
                    }
                 })
                } else {
                    this.addressList.forEach((item, index) => {
                    this.addressFilter.push(this.addressList[index])
                 })
                }
                this.searchShow = false
            }
        }
        
    }
</script>
<style lang="stylus">
    .map-wrap
        width 100%
        height 100%
    #map
        width 100%
        height 100%

    .icon-blue  
        color #50c3c1
     
    .icon-gray  
        color gray
    
    .icon-green  
        color green
     
    .icon-yellow 
        color yellow
     
    .icon-yellow2 
        color #faac52
     
    .icon-red 
        color red
     
    .home-btn  
        display block;
        width 5rem
        height 2.5rem
        font-size 1.2rem;
        color white;
        margin 0
        padding 0
        outline none
        text-decoration none
        border-radius 20%
        background-color #747472
        opacity 0.9
        &.icon-charger-filter:before
            font-size 1.2rem
        position fixed;
        top 3%;
        left 14%;
     
</style>

